class Subscribe extends React.Component {
    onFinish = (values) => {
      console.log('Success:', values);
      console.log(values.EMAIL);
      

      // const form = document.createElement('form');
      // form.method = "post";
      // form.action = "https://gmail.us1.list-manage.com/subscribe/post?u=34472a49ae8c29513d3b8c093&amp;id=460089d3be";

      // for (const key in values) {
      //   if (values.hasOwnProperty(key)) {
      //     const hiddenField = document.createElement('input');
      //     hiddenField.type = 'hidden';
      //     hiddenField.name = key;
      //     hiddenField.value = values[key];

      //     form.appendChild(hiddenField);
      //   }
      // }

      // document.body.appendChild(form);
      // form.submit();
      // console.log('Success:', values);
    };
  
    onFinishFailed = (errorInfo) => {
      console.log('Failed:', errorInfo);
    };
    //action="https://gmail.us1.list-manage.com/subscribe/post?u=34472a49ae8c29513d3b8c093&amp;id=460089d3be"  method="post" 

    constructor(props) {
      super(props);
      this.state = { FNAME: '', LNAME: '', EMAIL: '' };
    }
    submitHandler = (event) => {
      // event.preventDefault(e);
      // console.log("submit called");
      // console.log(this.state);
      // alert("You are submitting " + this.state);

      // const form = document.createElement('form');
      // form.method = "post";
      // form.action = "https://gmail.us1.list-manage.com/subscribe/post?u=34472a49ae8c29513d3b8c093&amp;id=460089d3be";


      fetch('https://gmail.us1.list-manage.com/subscribe/post?u=34472a49ae8c29513d3b8c093&amp;id=460089d3be', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        mode: 'no-cors',
        body: JSON.stringify(this.state),
      }).then((response) => {
        // console.log('response', response);
        const successDiv = document.getElementById('mce-success-response');
        successDiv.style.display = 'initial';
        const p = document.createElement('p');
        p.style.paddingBottom = "15px";
        p.style.color = "#ffffff";
        p.innerHTML = "Thank You!";
        successDiv.appendChild(p);

        setTimeout( function() {
          successDiv.style.display='none';
          successDiv.innerHTML = "";
        }, 2021);
      }).catch((error) => {
        // console.log('error', error);
      });


      // for (const key in this.state) {
      //   if (this.state.hasOwnProperty(key)) {
      //     const hiddenField = document.createElement('input');
      //     hiddenField.type = 'hidden';
      //     hiddenField.name = key;
      //     hiddenField.value = this.state[key];

      //     form.appendChild(hiddenField);
      //   }
      // }

      // document.body.appendChild(form);
      // form.submit();
      // console.log('Success:', this.state);
    }
    emailHandler = (event) => {
      this.setState({EMAIL: event.target.value});
      // console.log(this.state);
    }
    fnameHandler = (event) => {
      this.setState({FNAME: event.target.value});
      // console.log(this.state);
    }
    lnameHandler = (event) => {
      this.setState({LNAME: event.target.value});
      // console.log(this.state);
    }

    render() {
      return (
        <Container>
          <Form
              {...layout}
              initialValues={{
                  remember: true,
              }}
              onFinish={this.submitHandler}
              onFinishFailed={this.onFinishFailed}
              novalidate
              onSubmit={this.submitHandler}
              >
              <div class="intro" style={{ marginBottom: "20px" }}>
                  <h2 class="text-center" style={{ color: "#ffda59" }}>Subscribe to our Mailing List</h2>
                  <p class="text-center" style={{ color: "#ffffff", paddingTop: "5px" }}>Can't be bothered to check social media just to see what we're up to? No need to worry! Just subscribe to our mailing list and we'll keep you updated on everything you have to look forward to!<br/></p>
              </div>
              <Form.Item name="First Name" rules={[{ required: true }]} style={{ display: "inline-block", width: "49%", marginRight: "1%"}}>
                  <Input onChange={this.fnameHandler} placeholder="First Name" type="text" value="" name="FNAME" class="" id="mce-FNAME" style={{ padding: ".375rem .75rem", background: "#eff1f4", border: "none", borderRadius: "3px", boxShadow: "none", outline: "0", color: "inherit", textIndent: "9px", height: "45px" }}/>
              </Form.Item>
              <Form.Item name="Last Name" rules={[{ required: true }]} style={{ display: "inline-block", width: "49%", marginLeft: "1%" }}>
                  <Input onChange={this.lnameHandler} placeholder="Last Name" type="text" value="" name="LNAME" class="" id="mce-LNAME" style={{ padding: ".375rem .75rem", background: "#eff1f4", border: "none", borderRadius: "3px", boxShadow: "none", outline: "0", color: "inherit", textIndent: "9px", height: "45px" }}/>
              </Form.Item>
              <Form.Item name="Email" rules={[{ required: true }]}>
                  <Input onChange={this.emailHandler} placeholder="Email" type="email" value="" name="EMAIL" class="required email" id="mce-EMAIL" style={{ padding: ".375rem .75rem", background: "#eff1f4", border: "none", borderRadius: "3px", boxShadow: "none", outline: "0", color: "inherit", textIndent: "9px", height: "45px" }}/>
              </Form.Item>
              <Form.Item {...tailLayout}>
              <div class="response" id="mce-error-response" style={{display:"none"}}></div>
		          <div class="response" id="mce-success-response" style={{display:"none"}}></div>
                  {/* <Button type="primary" htmlType="submit" style={{ border: "none", boxShadow: "none", textShadow: "none", opacity: "1", textTransform: "uppercase", fontWeight: "700", fontSize: "13px", letterSpacing: ".4px", lineHeight: "1", backgroundColor: "#008fe1!important", color: "#ffffff", outline: "0!important" }}>
                      Subscribe
                  </Button> */}
                  {/* <Input type="text" name="b_34472a49ae8c29513d3b8c093_460089d3be" tabindex="-1" value=""></Input> */}
                  <Input type="submit" value="Subscribe" name="subscribe" id="mc-embedded-subscribe" class="button" style={{width: "105px", padding: "6px", textTransform: "uppercase", fontWeight: "700", fontSize: "13px", letterSpacing: ".4px", backgroundColor: "#008fe1", color: "#ffffff", outline: "0 !important", border: "none"}}></Input>
              </Form.Item>
          </Form>
        </Container>
    );
    }
};

export default Subscribe;





