import React from 'react';
import { graphql } from 'gatsby';
import Layout from '../components/layout';
import CarouselGatsby from '../components/carousel';
import MissionRow from '../components/mission';
import HomeAbout from '../components/home_about';
import HomeTeam from '../components/home_team';
import Subscribe from '../components/subscribe';
import Instagram from '../components/instagram';
import PowrInstagram from '../components/powr-instagram';
import Partners from '../components/partners';
import 'antd/dist/antd.css';

const HomePage = ({ data }) => {
  const title = data.markdownRemark.frontmatter.title;

  return (
    <Layout title={title}>
      <div id="carouselContainer" style={{ padding: "0px", background: "#013b89" }}>
        <CarouselGatsby />
      </div>
      <HomeAbout/>
      <MissionRow/>
      {/* <Instagram/> */}
      {/* <PowrInstagram/> */}
      <Partners/>
      <HomeTeam/>
      {/* <Subscribe/> */}
    </Layout>
  );
};

export default HomePage;

export const pageQuery = graphql`
  query($slug: String!) {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(
      filter: { fields: { contentType: { eq: "posts" } } }
      sort: { order: DESC, fields: frontmatter___date }
      limit: 9
    ) {
      nodes {
        fields {
          slug
        }
        excerpt
        timeToRead
        frontmatter {
          date(formatString: "MMMM DD, YYYY")
          description
          title
          tags
        }
      }
    }
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        title
      }
    }
  }
`;
